<template>
  <main class="add-property pb-3">
    <b-container>
      <b-row>
        <b-col cols="12">
          <div class="navigation">
            <!-- <Stepper :length="6" /> -->
          </div>
          <div class="form mx-auto">
            <b-row>
              <b-col cols="12">
                <NuxtChild :propertyData="propertyData" />
              </b-col>
            </b-row>
          </div>
        </b-col>
      </b-row>
    </b-container>
    <WarningModal
      :show="showWarningModal"
      :routeTo="routeTo"
      @closeModal="showWarningModal = false"
      @closeModalAndNavigate="closeModalAndNavigate" />
  </main>
</template>

<script>
import Stepper from '@/components/AddProperty/Stepper';
import WarningModal from '@/components/AddProperty/WarningModal';

import { fetchPropertiesData } from '@/api/properties';
export default {
  name: 'AddPropertyPage',
  components: {
    Stepper,
    WarningModal,
  },
  data() {
    return {
      propertyData: {},
      showWarningModal: false,
      routeTo: '',
      navigate: false,
    };
  },
  computed: {
    isRTL() {
      return this.$store.getters['CoreModule/isRTL'];
    },
    currentPage() {
      return parseInt(this.$route?.path?.match(/(\d+)/)[0]);
    },
  },
  async fetch() {
    await this.getPropertiesData();
  },
  methods: {
    async getPropertiesData() {
      try {
        const response = await fetchPropertiesData();

        this.propertyData = response.Data;
      } catch (error) {
        console.log(error);
      }
    },
    closeModalAndNavigate() {
      this.showWarningModal = false;
      this.navigate = true;
    },
  },
  beforeRouteLeave(to, from, next) {
    if (
      !to.path.includes('/properties/add/') &&
      localStorage.getItem('property')
    ) {
      this.showWarningModal = true;
      this.routeTo = to.path;

      if (this.navigate) return next();
    } else next();
  },
};
</script>

<style lang="scss">
.add-property {
  min-height: calc(100vh - 48px - 48px);
  background-color: #f7f9fa;
  // padding: 48px 0;

  .container {
    height: 100%;

    .form {
      width: 700px;
      .form-container {
        width: 100%;
        height: 100%;
        padding: 32px;
        margin: 16px 0;
        border-radius: 16px;
        background-color: #fff;

        .form-heading {
          text-align: center;
          font-size: 24px;
          font-weight: bold;
          margin: 16px 0;
          color: #111;
        }

        .form-input {
          padding: 18px 0;
          border-bottom: 1px solid #f2f4f5;

          &:last-child {
            border: none;
          }

          label {
            font-size: 16px;
            font-weight: bold;
          }

          .v-select {
            svg {
              all: unset;
            }
          }

          .chips-container {
            margin: 0;
          }

          .number-counter-input {
            display: flex;
            justify-content: space-between;
            align-items: center;
          }

          input.form-input {
            padding: 24px 16px;
            border: 1px solid #e3e5e5;
            border-radius: 10px;
          }
        }
      }
    }

    .page-actions {
      display: flex;
      justify-content: center;
      align-items: center;

      .btn {
        width: 280px;
        padding: 12px 0;
        font-weight: bold;
        border-radius: 8px;
        border: none;
        margin: 8px;

        &-primary {
          color: #fff;
          background-color: #0078ff;
        }

        &-secondary {
          color: #111;
          background-color: #fff;
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .add-property {
    .container {
      .container {
        padding: 0;
      }
      .form {
        width: 100%;
      }

      .page-actions {
        flex-direction: column-reverse;
      }
    }
  }
}
</style>
